import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import "components/css/MyPage.css";
import { dbService, authService } from "fbase";
import { faUser } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "components/css/MyPage.css";

const MyPage = ({ userObj }) => {
	const history = useHistory();
	const [myWebtoon, setMyWebtoon] = useState([]);
	const [test, setTest] = useState([]);
	var newArr=[];
	
	useEffect(()=>{
		onCreate();
	},[])

	const onCreate = async() => {
		await dbService.collection("webtoon")
		.get().then((querySnapshot) => {
			querySnapshot.forEach((doc) => {
				if(doc.data().userId === userObj.uid) {
					newArr.push({
						key : doc.data().webtoonId,
						url : doc.data().url,
						image: doc.data().image,
						title: doc.data().title, 
						reader: doc.data().reader, 
						category: doc.data().category, 
						author: doc.data().author, 
						weekday: doc.data().weekday
					});
					setMyWebtoon(newArr);
					// ì•„ë˜ ì½”ë“œëŠ” ì˜ë¯¸ì—†ëŠ” ì½”ë“œ.. ì›ì¸ì„ ëª¨ë¥´ê² ìŒ.
					setTest(test.concat({
						key : doc.data().webtoonId,
						url : doc.data().url,
						image: doc.data().image,
						title: doc.data().title, 
						reader: doc.data().reader, 
						category: doc.data().category, 
						author: doc.data().author, 
						weekday: doc.data().weekday
					}))
				}
			})
		})
	}

	const onClickLogOut = () => {
		authService.signOut();
		history.push("/");
		window.alert("ë¡œê·¸ì•„ì›ƒ ë˜ì—ˆìŠµë‹ˆë‹¤.");
	}


	return (
		<>
    <div className="mypage_container">
	    <div className="account_title">ë¡œê·¸ì¸í•œ ê³„ì •</div>
			<div className="account_container">
				<img className="account_img" src={userObj._delegate.photoURL} />
				<div className="account_info">
					<div className="account_des">ì´ë¦„ : {userObj._delegate.displayName}</div>
					<div className="account_des">ë©”ì¼ : {userObj._delegate.email}</div>
					<button className="account_logBtn" onClick={onClickLogOut}>ë¡œê·¸ì•„ì›ƒ</button>
				</div>
			</div>
    </div>
		<div className="mypage_container">
			<div className="fav_title">ì¦ê²¨ì°¾ê¸°í•œ ì›¹íˆ°</div>
			{myWebtoon.map((webtoon) => (
				<FavWebtoon
					key={webtoon.key}
					webtoon = {webtoon}
				/>)
			)}
		</div>
		</>
  )
}

const FavWebtoon = ({webtoon}) => {
	
	return(
		<>
			<div className="container">
        <a className="fav_info_container" href={webtoon.url}>
          <img className="fav_image" src={webtoon.image} />
          <div className="fav_info_main">
            <h2 className="fav_info_title">{webtoon.title}</h2>
            <div className="fav_info_sub">
              <FontAwesomeIcon className="user_icon" icon={faUser} />
              {`${((webtoon.reader) / 10000).toFixed(1)}ë§Œëª… | ${webtoon.category} | ${webtoon.author}`}
            </div>
            <div className="fav_info_sub">
              {`ì—°ì¬ ìš”ì¼ / ${webtoon.weekday}`}
            </div>
          </div>
        </a>
      </div>
    </>
	)
}

export default MyPage;