import React from "react";
import "components/css/Weekly.css";
import ResultWebtoon from "./ResultWebtoon";

const WeekWebtoon = ({ webtoons, path }) => {
    let weekday;

    switch (path) {
        case "mon": { weekday = "ì›”"; break; }
        case "tue": { weekday = "í™”"; break; }
        case "wed": { weekday = "ìˆ˜"; break; }
        case "thu": { weekday = "ëª©"; break; }
        case "fri": { weekday = "ê¸ˆ"; break; }
        case "sat": { weekday = "í† "; break; }
        case "sun": { weekday = "ì¼"; break; }
    }

    let weekWebtoon = webtoons.filter((webtoon) => (webtoon.finished === 0))
        .filter((element) => element.pubperiod === weekday)
    const weekWebtoonCount = weekWebtoon.length;

    for (let i = 1; i < 5; i++) {
        if (weekWebtoonCount % 5 === i) {
            for (let j = i; j < 5; j++) {
                weekWebtoon.push({
                    "series_id": j,
                    "title": "",
                    "image": "",
                    "read_count": 0,
                    "url": ""
                })
            }
        }
    }

    return (
        <div className="weekly_main_container">
            <h2 className="weekly_count">
                {`ì „ì²´ (${weekWebtoonCount})`}
            </h2>
            <div className="weekly_container">
                {weekWebtoon
                    .map((webtoon) => (
                        <ResultWebtoon
                            key={webtoon.series_id}
                            url={webtoon.url}
                            image={webtoon.image}
                            title={webtoon.title}
                            reader={webtoon.read_count}
                        />

                    ))
                }
            </div>
        </div>
    )
}

export default WeekWebtoon;