import React, { useEffect, useRef, useState } from 'react';
import "components/css/Slider.css";

// ì „ì²´ ìŠ¬ë¼ì´ë“œ ê°œìˆ˜ (ì´3ê°œ. ë°°ì—´ë¡œ ê³„ì‚°)

export default function EventSlider({info}) {
  const display_event = info.filter((element) => element.display === 1);
  const [currentSlide, setCurrentSlide] = useState(0);
  const slideRef = useRef(null);
  const TOTAL_SLIDES = display_event.length;


  // Next ë²„íŠ¼ í´ë¦­ ì‹œ
  const NextSlide = () => {
    if (currentSlide >= TOTAL_SLIDES-1) {
      // ë” ì´ìƒ ë„˜ì–´ê°ˆ ìŠ¬ë¼ì´ë“œê°€ ì—†ìœ¼ë©´
      setCurrentSlide(0); // 1ë²ˆì§¸ ì‚¬ì§„ìœ¼ë¡œ ë„˜ì–´ê°‘ë‹ˆë‹¤.
    } else {
      setCurrentSlide(currentSlide + 1);
    }
  };
  // Prev ë²„íŠ¼ í´ë¦­ ì‹œ
  const PrevSlide = () => {
    if (currentSlide === 0) {
      setCurrentSlide(TOTAL_SLIDES - 1); // ë§ˆì§€ë§‰ ì‚¬ì§„ìœ¼ë¡œ ë„˜ì–´ê°‘ë‹ˆë‹¤.
    } else {
      setCurrentSlide(currentSlide - 1);
    }
  };

  useEffect(() => {
    slideRef.current.style.transition = 'all 0.5s ease-in-out';
    slideRef.current.style.transform = `translateX(-${currentSlide}00%)`; // ë°±í‹±ì„ ì‚¬ìš©í•˜ì—¬ ìŠ¬ë¼ì´ë“œë¡œ ì´ë™í•˜ëŠ” ì—ë‹ˆë©”ì´ì…˜ì„ ë§Œë“­ë‹ˆë‹¤.
  }, [currentSlide]);
  return (
    <div className="slide_main_container event">
      <div className="slide_container" ref={slideRef}>
        {display_event.map((element) => (
          <img className="slide_event_img" src={element.image} key={element.uid}/>
        ))}
      </div>
      <div className="slide_btns">
        <button className="btn_event left" onClick={PrevSlide}>â—€</button>
        <button className="btn_event right" onClick={NextSlide}>â–¶</button>
      </div>
    </div>
  );
}